import { type FC } from 'react';
import { type SubmitHandler, useForm } from 'react-hook-form';
import { useMutation } from '@apollo/client';
import { OPERATION_ADD } from '@/graphql/mutations';
import { useCategories } from '@/hooks';
import { Button, Input, Select } from '@/ui';
import { useApolloErrorHandler } from '@/apollo/useApolloErrorHandler';
import { useToast } from '@/servi—Åes/ToastContext';
import type { OperationAddFormProps, OperationAddFormSentProps } from './OperationAddForm.types';
import styles from './OperationAddForm.module.css';

const OperationAddForm: FC<OperationAddFormProps> = ({ closeFN }) => {
  const { show } = useToast();
  const { handleQueryErrors } = useApolloErrorHandler();

  const {
    formState: { errors },
    handleSubmit,
    register,
    setValue,
  } = useForm<OperationAddFormSentProps>({
    defaultValues: {
      name: '',
      desc: '',
      amount: 0,
      date: new Date().getDate().toString(),
      category: '',
      type: 'Cost',
    },
  });

  const { categories, loading } = useCategories();

  const [patch] = useMutation(OPERATION_ADD);

  const submit: SubmitHandler<OperationAddFormSentProps> = async (el) => {
    try {
      await patch({
        variables: {
          input: {
            name: el.name,
            desc: el.desc,
            amount: Number(el.amount),
            categoryId: el.category,
            type: el.type,
            date: el.date,
          },
        },
      });
      closeFN();
    } catch (error) {
      const processedErrors = handleQueryErrors(error);
      show(processedErrors, 'error');
    }
  };

  if (loading) return <div>–ó–∞–≥—Ä—É–∑–∫–∞...</div>;

  return (
    <form className={styles.form} onSubmit={handleSubmit(submit)}>
      <div className={styles.field}>
        <Input
          error={errors.name?.message}
          label="–ù–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ"
          {...register('name', {
            required: '–ù–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ –ø–æ–ª–µ',
          })}
        />
      </div>
      <div className={styles.field}>
        <Input label="–û–ø–∏—Å–∞–Ω–∏–µ" {...register('desc')} />
      </div>
      <div className={styles.field}>
        <Input label="–°—Ç–æ–∏–º–æ—Å—Ç—å" {...register('amount')} />
      </div>
      <div className={styles.field}>
        <Input
          error={errors.date?.message}
          label="–î–∞—Ç–∞"
          type="date"
          {...register('date', {
            required: '–ù–µ –≤—ã–±—Ä–∞–Ω–∞ –¥–∞—Ç–∞',
          })}
        />
      </div>
      <div className={styles.field}>
        <Select
          label="–ö–∞—Ç–µ–≥–æ—Ä–∏—è"
          options={[
            { id: 'Cost', name: 'Cost' },
            { id: 'Profit', name: 'Profit' },
          ]}
          {...register('type')}
          onChange={(id) => {
            setValue('type', id as 'Cost' | 'Profit');
          }}
        />
      </div>
      <div className={styles.field}>
        <Select
          label="–ö–∞—Ç–µ–≥–æ—Ä–∏—è"
          options={categories}
          {...register('category', {
            required: '–ù–µ –≤—ã–±—Ä–∞–Ω–∞ –∫–∞—Ç–µ–≥–æ—Ä–∏—è',
          })}
          onChange={(id) => {
            setValue('category', id);
          }}
        />
      </div>
      <Button text="–î–æ–±–∞–≤–∏—Ç—å" type={'submit'} />
    </form>
  );
};

export { OperationAddForm, type OperationAddFormProps };
