import { type SubmitHandler, useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import { useMutation } from '@apollo/client';
import { SIGN_UP } from '@/graphql';
import { useAuth } from '@/stores/useAuth';
import { Button, Input } from '@/ui';
import { useApolloErrorHandler } from '@/apollo/useApolloErrorHandler';
import { useToast } from '@/servi—Åes/ToastContext';
import type { FormRegistrationData } from './FormRegistration.types';
import styles from './FormRegistration.module.css';

const FormRegistration = () => {
  const { show } = useToast();
  const { handleQueryErrors } = useApolloErrorHandler();

  const {
    formState: { errors, isValid },
    handleSubmit,
    register,
    watch,
  } = useForm<FormRegistrationData>({ mode: 'onChange' });
  const { login } = useAuth();

  const [signup, { error: gqlError, loading }] = useMutation(SIGN_UP, {
    onCompleted: (data) => {
      login(data.profile.signup.token);
    },
    onError: (error) => {
      const processedErrors = handleQueryErrors(error);
      show(processedErrors, 'error');
    },
  });

  const password = watch('password');

  const onSubmit: SubmitHandler<FormRegistrationData> = async (data) => {
    try {
      await signup({
        variables: {
          email: data.email,
          password: data.password,
          commandId: '7808',
        },
      });
    } catch (error) {
      const processedErrors = handleQueryErrors(error);
      show(processedErrors, 'error');
    }
  };

  return (
    <div className={styles.registrationContainer}>
      <h2 className={styles.title}>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
      <form className={styles.form} data-testid="register-form" onSubmit={handleSubmit(onSubmit)}>
        <Input
          error={errors.email?.message}
          id="email"
          label="Email"
          type="email"
          {...register('email', {
            required: 'Email –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω',
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: '–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π email',
            },
          })}
        />

        <Input
          error={errors.password?.message}
          id="password"
          label="–ü–∞—Ä–æ–ª—å"
          type="password"
          {...register('password', {
            required: '–ü–∞—Ä–æ–ª—å –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω',
            minLength: {
              value: 8,
              message: '–ú–∏–Ω–∏–º—É–º 8 —Å–∏–º–≤–æ–ª–æ–≤',
            },
            validate: (value) => /[A-Z]/.test(value) || '–•–æ—Ç—è –±—ã –æ–¥–Ω–∞ –∑–∞–≥–ª–∞–≤–Ω–∞—è –±—É–∫–≤–∞',
          })}
        />

        <Input
          error={errors.confirmPassword?.message}
          id="repeatPassword"
          label="–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
          type="password"
          {...register('confirmPassword', {
            required: '–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –ø–∞—Ä–æ–ª—å',
            validate: (value) => value === password || '–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç',
          })}
        />

        <Link to="/login">–ê–≤—Ç–æ—Ä–∏–∑–æ–≤–∞—Ç—å—Å—è</Link>

        <Button
          color={'Primary'}
          disabled={!isValid || loading}
          text={loading ? '–ó–∞–≥—Ä—É–∑–∫–∞...' : '–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è'}
          type={'submit'}
        />

        {gqlError && <p className={styles.error}>{gqlError.message}</p>}
      </form>
    </div>
  );
};

export { FormRegistration, type FormRegistrationData };
