type ServerError = {
  extensions: {
    code: ErrorCode;
    fieldName?: string;
    stacktrace: string;
  };
  name: string;
  message: string;
};

type ProcessedError = {
  code: ErrorCode;
  message: string;
  field?: string;
  original: ServerError;
};

type ServerErrors = {
  errors: ServerError[];
};

export { ErrorCode, type ServerError, type ServerErrors, type ProcessedError };
